<template>
  <v-container grid-list-xs>
    <v-row>
      <v-col align="center">
        <v-card
          class="pa-2 mt-5"
          max-width="800"
          color="blue-grey lighten-5"
          outlined
          :elevation="10"
        >
          <v-subheader class="overline">
            <v-icon class="mr-5">fa-heart</v-icon>Favoritos
          </v-subheader>
          <div v-if="contadorFavoritos > 0">
            <v-expand-transition>
              <v-card>
                  <v-slide-x-transition
                    class="py-0"
                    group
                    tag="v-list"
                    leave-absolute
                    name="d"
                  >
                  <template v-for="(producto, index) in listaFavoritosCompleta">
                    <v-list-item :key="producto._id">
                      <v-list-item-avatar tile>
                        <v-img :src="getIMG(producto.img)"></v-img>
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title
                          class="font-weight-regular text-left mr-12"
                          v-html="producto.descripcion"
                        />
                        <v-list-item-subtitle
                          class="font-weight-medium text-left"
                          v-html="'PRECIO: ' + formatMoney(producto.precio)"
                        />
                        <v-list-item-subtitle
                          class="text-left overline"
                          v-html="producto.existencia + ' disponibles'"
                        />
                      </v-list-item-content>
                      <v-menu bottom left>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn icon v-bind="attrs" v-on="on">
                            <v-icon>fa-ellipsis-v</v-icon>
                          </v-btn>
                        </template>
                        <v-list class="py-0">
                          <v-list-item @click>
                            <v-list-item-title>Comprar</v-list-item-title>
                          </v-list-item>
                          <v-divider />
                          <v-list-item @click="addToCart(producto)">
                            <v-list-item-title>Añadir al carrito</v-list-item-title>
                          </v-list-item>
                          <v-divider />
                          <v-list-item @click="eliminarProducto(producto)">
                            <v-list-item-title class="red--text">Eliminar</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </v-list-item>
                    <v-divider v-if="index + 1 < contadorFavoritos" :key="index" />
                  </template>
                  </v-slide-x-transition>
              </v-card>
            </v-expand-transition>
          </div>
          <div v-else>
              <v-card-text class="pt-0">
                  <v-subheader class="pt-0">No hay produtos que mostrar</v-subheader>
                  <v-btn color="success" text @click="$router.push('/')">agregar productos</v-btn>
              </v-card-text>
            </div>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  async created() {
    this.$store.commit("navigate", false);
    await this.$store.dispatch(
      "favoritos/getFavoritos",
      this.$store.state.auth.usuario.token
    );
    setTimeout(()=>this.$store.commit("favoritos/getListaFavoritosCompleta"),1500)
  },
  computed: {
    ...mapState("favoritos", ["listaFavoritosCompleta", "contadorFavoritos"]),
  },
  methods: {
    getIMG(id) {
      return require(`@/assets/products/${id}`);
    },
    formatMoney(mount) {
      return mount.toLocaleString("en-US", {
        style: "currency",
        currency: "MXN",
      });
    },
    openProduct(id) {
      this.$router.push("/product/" + id);
    },
    async eliminarProducto(producto) {
      await this.$store.dispatch("favoritos/eliminarFavorito", {
        id: this.$store.state.auth.usuario.token,
        product: producto,
      });
    },
    addToCart(producto){
      this.$store.dispatch('shopping_cart/agregarAlCarrito', {
        producto: producto._id,
        cantidad: 1,
        precio: producto.precio,
      })
    }
  },
};
</script>